/*!
 * Josh's Custom CSS Reset
 * https://www.joshwcomeau.com/css/custom-css-reset/ */
*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
}

body {
  -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

html {
  scroll-behavior: smooth;
}

body {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

main {
  display: block;
}

li {
  list-style: none;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 16px;
  font-weight: bold;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  vertical-align: middle;
  width: 100%;
  height: auto;
}

address {
  font-style: normal;
}

sup {
  vertical-align: super;
  font-size: smaller;
}

form {
  accent-color: #014c86;
}

button,
input,
textarea,
select {
  color: inherit;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
  text-transform: none;
  border-radius: 0;
}

input,
textarea,
select {
  background-color: #fff;
  border: solid 1px #e5e5e5;
  padding: 0.5em;
}
input::placeholder,
textarea::placeholder,
select::placeholder {
  color: #b5b5b5;
}
input:read-only, input:disabled,
textarea:read-only,
textarea:disabled,
select:read-only,
select:disabled {
  opacity: 0.4;
  cursor: not-allowed;
  outline: none;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px #fff inset;
}
input[type=number] {
  -moz-appearance: textfield;
}
input[type=number]::-webkit-outer-spin-button, input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type=radio], input[type=checkbox] {
  cursor: pointer;
}

textarea {
  min-height: 100px;
  resize: vertical;
}

select {
  background: url(data:image/png;base64,) right 5px center/16px no-repeat;
  padding-right: 20px;
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
select::-ms-expand {
  display: none;
}

button,
input[type=submit],
input[type=reset] {
  background: none;
  border: none;
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

label {
  cursor: pointer;
}

label.__radio input[type=radio], label.__checkbox input[type=checkbox] {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
}

label.__checkbox {
  display: block;
}
label.__checkbox input[type=checkbox] + span {
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
}
label.__checkbox input[type=checkbox] + span::before {
  content: "";
  display: block;
  background-color: #fff;
  border: solid 1px #fff;
  outline: solid 1px #d5d5d5;
  border-radius: 1px;
  width: 16px;
  height: 16px;
  margin-right: 11px;
}
label.__checkbox input[type=checkbox]:checked + span::before {
  background-color: #014c86;
  border: solid 2px #fff;
}

label.__radio {
  display: block;
}
label.__radio input[type=radio] + span {
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
}
label.__radio input[type=radio] + span::before {
  content: "";
  display: block;
  background-color: #fff;
  border-radius: 50%;
  outline: solid 2px #014c86;
  width: 16px;
  height: 16px;
  margin-right: 11px;
}
label.__radio input[type=radio]:checked + span::before {
  background-color: #014c86;
  border: solid 3px #fff;
  outline: solid 2px #014c86;
}

body {
  color: #333;
  font-size: 16px;
  font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  line-height: 1.7;
}

.l-inner {
  max-width: 1024px;
  margin-left: auto;
  margin-right: auto;
}

.l-header {
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
  align-items: center;
}
.l-header__logo {
  flex: 0 0 auto;
  width: 80px;
}

.l-index {
  overflow-x: hidden;
}

.p-header-nav {
  display: flex;
  align-items: center;
}
.p-header-nav__item + * {
  margin-left: 20px;
}

.p-index-mv {
  position: relative;
}
.p-index-mv__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #fff;
  padding: 120px 80px;
  min-height: 500px;
}
.p-index-mv__title {
  font-size: 64px;
  line-height: 1.35;
}
.p-index-mv__lead {
  font-size: 20px;
  margin-top: 20px;
}
.p-index-mv__bg {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}
.p-index-mv__bg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

